.box_list_data {
  width: 100%; }
.list_data {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  li {
    display: inline-block;
    padding: 5px;
    margin: 5px;
    width: 37px;
    height: 37px;
    color: white;
    text-align: center;
    &.active {
      background-color: darkorange;
      border: 1px solid darkorange; }
    p {
      font-weight: 600;
      margin: 0;
      padding: 0; } } }
